import React from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";
import MenuItem from "@material-ui/core/MenuItem";
import MenuIcon from "@material-ui/icons/Menu";
import Hidden from "@material-ui/core/Hidden";

export default function Header(props) {
  const [drop, setDrop] = React.useState(false);

  const handleClick = (event) => {
    setDrop((prevDrop) => !prevDrop);
  };
  return (
    <AppBar position="static" className={props.classes.bar}>
      <div className={props.classes.drop}>
        <Toolbar>
          <IconButton
            edge="start"
            className={props.classes.menuButton}
            color="inherit"
            aria-label="menu"
          >
            {"<Adalberto />"}
          </IconButton>
          <Typography variant="h6" className={props.classes.title}></Typography>
          <Hidden smDown>
            <Button
              color="inherit"
              onClick={() => props.executeScroll("about")}
            >
              Sobre mim
            </Button>
            <Button
              color="inherit"
              onClick={() => props.executeScroll("skills")}
            >
              Habilidades
            </Button>
            <Button
              color="inherit"
              onClick={() => props.executeScroll("projects")}
            >
              Projetos
            </Button>
            <Button
              color="inherit"
              onClick={() => props.executeScroll("contact")}
            >
              Contato
            </Button>
          </Hidden>
          <Hidden mdUp>
            <Button onClick={handleClick}>
              <MenuIcon style={{ color: "white" }} />
            </Button>
          </Hidden>
        </Toolbar>
        <Hidden mdUp>
          <div style={drop ? { display: "block" } : { display: "none" }}>
            <div id="simple-menu"></div>
            <div style={{ width: "100%" }}>
              <MenuItem onClick={() => props.executeScroll("about")}>
                Sobre mim
              </MenuItem>
              <MenuItem onClick={() => props.executeScroll("skills")}>
                Habilidades
              </MenuItem>
              <MenuItem onClick={() => props.executeScroll("projects")}>
                Projetos
              </MenuItem>
              <MenuItem onClick={() => props.executeScroll("contact")}>
                Contato
              </MenuItem>
            </div>
          </div>
        </Hidden>
      </div>
    </AppBar>
  );
}
