import React from "react";
import Hotscreen from "../../imgs/hotscreen.png";
import Photosocial from "../../imgs/photosocial.png";
import Grid from "@material-ui/core/Grid";

const grid = (title, text, img, url, i) => {
  if (i === 0) {
    return (
      <div>
        <div className="subtitle">{title}</div>
        <Grid container spacing={3} alignItems="center" justify="space-around">
          <Grid item sm={12} md={8}>
            {url ? (
              <a target="_blank" rel="noopener noreferrer" href={url}>
                <img src={img} style={{ maxWidth: "80%" }} alt="" />
              </a>
            ) : (
              <img src={img} style={{ maxWidth: "80%" }} alt="" />
            )}
          </Grid>
          <Grid item sm={12} md={4}>
            <p>{text}</p>
          </Grid>
        </Grid>
      </div>
    );
  }
  return (
    <div>
      <div className="subtitle">{title}</div>
      <Grid container spacing={3} alignItems="center" justify="space-around">
        <Grid item sm={12} md={4}>
          <p>{text}</p>
        </Grid>
        <Grid item sm={12} md={8}>
          {url ? (
            <a target="_blank" rel="noopener noreferrer" href={url}>
              <img src={img} style={{ maxWidth: "80%" }} alt="" />
            </a>
          ) : (
            <img src={img} style={{ maxWidth: "80%" }} alt="" />
          )}
        </Grid>
      </Grid>
    </div>
  );
};

export default function Projects() {
  return (
    <div>
      <div className="title">{"<Projetos />"}</div>
      {grid(
        "HotScreen",
        "Uma plataforma de streaming inspirada na Netflix",
        Hotscreen,
        "https://hotscreen-9beab.web.app/",
        1
      )}
      <br />
      {grid(
        "PhotoSocial",
        "Uma rede social para compartilhamento de imagens inspirado no Instagram",
        Photosocial,
        null,
        0
      )}
      <br />
      Veja mais no meu{" "}
      <a target="_blank" rel="noopener noreferrer" className="link" href="http://github.com/thedarkzeno">
        github
      </a>
    </div>
  );
}
